.nav-bar {
    position: relative;
    height: 6.25rem;
    width: calc(90vm - 1rem);
    max-width: calc(1200px - 1rem);
    margin: 0;
    padding: 0.95rem max(5vw, calc(50vw - 600px));
    display: flex;
    align-items: center;
    gap: 2.5rem;
    z-index: 1;
    a {
        padding-left: 35px;
        outline: none;
        text-decoration: none;
        color: #a200ff;
    }
    a:visited {
        color: #a200ff;
    }
    a:hover {
        color: #00ff00;
    }

    @media (min-aspect-ratio: 1/1) {
        .menu {
            display: flex;
            width: 60%;
            align-items: center;
            gap: 2.5rem;
        }
        .mobileMenu {
            display: none;
        }
    }
    @media (max-aspect-ratio: 1/1) {
        height: 35px;
        .menu {
            position: absolute;
            width: calc(90vm - 1rem);
            max-width: 1200px;
            margin: 0;
            padding: 0.95rem max(5vw, calc(50vw - 600px));
            top: 100%;
            left: 0;
            background-color: #fff;
            display: flex;
            flex-direction: column;
            gap: 1rem;
            transition: opacity 0.5s;
        }
        .mobileMenu {
            position: relative;
            width: min(7.5vw, 5vh);
            height: min(7.5vw, 5vh);
        }
        .mobileMenu::before,
        .mobileMenu::after {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            font-family: 'Material Symbols Outlined';
            font-size: min(7.5vw, 5vh);
            line-height: min(7.5vw, 5vh);
            text-align: center;
            transition:
                opacity 0.5s,
                transform 0.5s;
        }
        .mobileMenu::before {
            content: 'menu';
            color: #5f6367;
        }
        .mobileMenu::after {
            content: 'close';
            color: #000;
        }
    }
}
